import jsonp from 'common/js/jsonp'
import { commonParams, options } from './config'
import axios from 'axios'


export function getSingerList(index) {
    const url = "/api/getSingerList"
    const data = Object.assign({}, commonParams, {
        '-': 'getUCGI3106801329178803',
        platform: 'yqq.json',
        loginUin: 0,
        hostUin: 0,
        needNewCode: 0,
        format: 'json',
        data: {
            "comm": {
                "ct": 24,
                "cv": 0
            },
            "singerList": {
                "module": "Music.SingerListServer",
                "method": "get_singer_list",
                "param": {
                    "area": -100,
                    "sex": -100,
                    "genre": -100,
                    "index": index,
                    "sin": 0,
                    "cur_page": 1
                }
            }
        }
    })
    return axios.get(url, {
        params: data
    }).then((res) => {
        return Promise.resolve(res)
    })
}

export function getSongListBySinger(id) {
    const url = "/api/getSongListBySinger"
    const data = Object.assign({}, commonParams, {
        '-': 'getSingerSong29951930724278997',
        platform: 'yqq.json',
        inCharset:'utf8',
        needNewCode: 0,
        loginUin: 1944522054,
        hostUin: 0,
        format:'json',
        data: {
            "comm": { "ct": 24, "cv": 0 },
            "singerSongList": {
                "method": "GetSingerSongList",
                "param": { "order": 1, "singerMid": id, "begin": 0, "num": 10 },
                "module": "musichall.song_list_server"
            }
        }
    });
    return axios.get(url, {
        params: data
    }).then((res) => {
        return Promise.resolve(res)
    })

}



