<template>
  <section>
    <scroll class="recommend-content" :data="singerList" ref="scroll">
      <div class="list">
        <div v-if="isShow" class="singer">
          <img :src="currSinger.singer_pic" width="100px" height="100px" />
          <div class="desc">
            <h1>{{currSinger.singer_name}}</h1>
            <div>
              单曲：
              <strong>{{currSinger.totalNum}}</strong>
            </div>
          </div>
        </div>
        <div v-show="!isShow">
          <div
            class="card"
            v-for="(item,index) in singerList"
            :key="index"
            @click="_getSongList(item)"
          >
            <div class="avtor">
              <img width="60px" height="60px" :src="item.singer_pic" />
            </div>
            <div class="desc">
              <h2>{{item.singer_name}}</h2>
              <img src="/static/imgs/rightg.png" width="20px" height="20px" />
            </div>
          </div>
        </div>
        <div v-show="isShow">
          <div
            class="card"
            v-for="(item,index) in songList"
            :key="index"
            @click="_getSongList(item)"
          >
            <div class="song">
              <h2>{{item.songInfo.name}}</h2>
              <div class="text">
                <p class="tag">独家</p>
                <h3>{{item.songInfo.singer[0].name}}·{{item.songInfo.album.name}}</h3>
              </div>
              <!-- <img src="/static/imgs/rightg.png" width="20px" height="20px" /> -->
            </div>
          </div>
        </div>
      </div>
    </scroll>
  </section>
</template>

<script type="text/ecmascript-6">
import { getSingerList, getSongListBySinger } from "api/singer";
import Scroll from "base/scroll/scroll";

export default {
  data() {
    return {
      singerList: [],
      currSinger: {},
      isShow: false, // 是否显示歌手详情
      songList: []
    };
  },
  components: { Scroll },
  mounted() {
    this._getSingerList(-100);
  },
  methods: {
    _getSingerList(index) {
      //获取歌手列表
      getSingerList(index).then(res => {
        this.singerList = res.data.singerList.data.singerlist;
      });
    },
    _getSongList(item) {
      this.currSinger = item;
      //获取歌曲
      getSongListBySinger(item.singer_mid).then(res => {
        this.songList = res.data.singerSongList.data.songList;
        this.currSinger.totalNum = res.data.singerSongList.data.totalNum;
        this.isShow = true;
        // console.log(res)
      });
    }
  }
};
</script>
<style lang="scss" scoped>
@import "~common/stylus/common";
section {
  position: fixed;
  top: 88px;
  width: 100%;
  bottom: 0;
}
.recommend-content {
  height: 100%;
  overflow: hidden;
  .singer {
    z-index: 5;
    display: flex;
    padding: 1rem 4rem;
    background-color: rgba(20, 20, 20, 0.01);
    img {
      border-radius: 50%;
    }
    .desc {
      font-size: 1.5rem;
      font-family: "Microsoft YaHei";
      line-height: 50px;
      padding-left: 2rem;
      div {
        font-size: 1rem;
        strong {
          font-size: 1.4rem;
        }
      }
    }
  }
  .list {
    transition-duration: 0.8s;
    .card {
      padding: 0.2rem 1.5rem;
      display: flex;
      .avtor {
        img {
          border-radius: 50%;
        }
      }
      .desc {
        width: 100%;
        padding: 0 0rem 0 1.5rem;
        line-height: 60px;
        color: $color-text;
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      .song {
        display: flex;
        flex-direction: column;
        width: 100%;
        padding: 0.5rem 0.4rem;
        .text {
          padding: 0.8rem 0;
          font-size: $font-size-small;
          color: $color-text-l;
          display: flex;
          h3 {
            line-height: 16px;
            margin-left: 5px;
          }
        }
      }
    }
  }
}
</style>





